.field {
  padding: 14px 0 13px 20px;
  border: 1px solid #e7e7e7;
  width: 100%;
  height: 45px;
  box-sizing: border-box;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  align-items: center;
  text-transform: uppercase;

  &::placeholder {
    color: $special_dark-grey;
  }

  &:hover {
    border: 1px solid $special_grey;
  }

  &:focus,
  &:focus-visible {
    outline: none;
    border: 2px solid $special_dark-grey;
  }

  &:disabled {
    border: 2px solid $status_error;
  }

  &--phone {
    padding: 15px 0 14px 15px;
    background-image: url("../img/cat-data/phone-icon.svg");
    background-repeat: no-repeat;
    background-position: center right 15px;
  }

  &--email {
    padding: 14px 0 13px 15px;
    background-image: url("../img/cat-data/email-icon.svg");
    background-repeat: no-repeat;
    background-position: center right 15px;
  }
}
